* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-family: 'Exo 2', 'Open Sans', sans-serif;
}

html {
  margin: 0;
  padding: 0;
}

body {
  padding: 0;
  margin: 0;
  letter-spacing: 1px;
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #042a2b;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{height:80px;width:100%;background-color:#07484b;display:flex;justify-content:space-between;align-items:center;padding-right:30px;padding-left:30px}.header h1{font-family:'Exo 2', sans-serif;color:#fcfcfc;font-weight:900}.add-item-icon{margin-top:10px;cursor:pointer;color:#fcfcfc}

.App{position:relative;text-align:center;margin:0;min-height:100%}.main{width:100%;height:100%;display:flex;padding:25px;flex-direction:column;justify-content:space-between}.refresh-button{cursor:pointer;height:70px;width:70px;background-color:#1e90ff;color:#fefefe;border-radius:50%;display:flex;justify-content:center;align-items:center;align-self:center;outline:none}@media screen and (min-width: 800px){.refresh-button:hover{background-color:#379bff}.refresh-button:active{transform:scale(0.98)}}.initial-create-container{width:100%;height:100%;display:flex;padding:25px;justify-content:center;align-items:center}.initial-create-button{cursor:pointer;color:#42b883}.initial-create-button div{font-size:18px;font-weight:300;margin-top:20px}

.item-form{padding:40px 20px;font-family:'Open Sans', sans-serif;letter-spacing:1px;display:flex;flex-direction:column;align-items:center;justify-content:center}.item-label{color:#fcfcfc;font-size:20px;text-align:left;width:100%;max-width:400px;margin:0 auto 20px auto}.item-input{width:100%;max-width:400px;padding:10px;background-color:#042a2b;color:#42b883;border:1px solid #42b883;margin-bottom:40px;font-size:16px;font-family:'Open Sans', sans-serif;letter-spacing:1px}.item-submit{cursor:pointer;width:55px;height:55px;background-image:linear-gradient(to bottom, #42B883, #4FC590);color:#fcfcfc;font-size:22px;border-radius:50%;border:none;outline:none;box-shadow:0px 6px 12px 0px rgba(0,0,0,0.2)}@media screen and (min-width: 800px){.item-submit:hover{background-image:linear-gradient(to bottom, #4FC590, #5CD29D)}.item-submit:active{transform:scale(0.98);outline:none}}

.tracking-list{font-family:'Open Sans', sans-serif;font-size:20px;font-weight:400;letter-spacing:1px}

.tracking-item-container{margin-bottom:20px;margin-top:10px}.theme-dark.color-checked{background-color:#42b883;border-color:#fcfcfc;color:#fcfcfc}.theme-dark.color-unchecked{background-color:#07484b;border-color:#fcfcfc;color:#fcfcfc}.tracking-item{cursor:pointer;display:flex;align-items:center;justify-content:space-between;border:1px solid;border-radius:5px;padding:10px 20px}.tracking-item-menu{height:45px;display:flex;justify-content:space-between;transition:height 0.3s linear}.tracking-item-menu-item{cursor:pointer;border:1px solid;border-top:none;width:100%;display:flex;justify-content:center;align-items:center}.hidden-menu{height:0}.visible-menu-item{opacity:1;transition:opacity 0.1s linear 0.2s}.hidden-menu-item{opacity:0;transition:opacity}

.bottom-menu-container{position:fixed;bottom:30px;right:30px}.bottom-menu-button{cursor:pointer;display:flex;align-items:center;justify-content:center;flex-direction:column-reverse;width:55px;height:55px;font-size:22px;border-radius:50%;background-image:linear-gradient(to bottom, #1e90ff, #2b9dff);color:#fcfcfc;box-shadow:0px 6px 12px 0px rgba(0,0,0,0.2);position:absolute;bottom:0;right:0;transition:transform 0.2s, bottom 0.2s, right 0.2s}@media screen and (min-width: 800px){.bottom-menu-button:hover{background-image:linear-gradient(to bottom, #2b9dff, #38aaff)}.bottom-menu-button:active{transform:scale(0.98);outline:none}}.close-button{background-image:linear-gradient(to bottom, #ef5b5b, #fc6868)}@media screen and (min-width: 800px){.close-button:hover{background-image:linear-gradient(to bottom, #fc6868, #ff7575)}.close-button:active{transform:scale(0.98)}}.rotate-0{transform:rotate(0deg)}.rotate-90{transform:rotate(90deg)}.rotate-90:active{transform:rotate(90deg)}.bottom-75{bottom:75px}.bottom-150{bottom:150px}.right-75{right:75px}.z-100{z-index:100}

