.tracking-item-container {
  margin-bottom: 20px;
  margin-top: 10px;
}

.theme-dark {
  &.color-checked {
    background-color: #42b883;
    border-color: #fcfcfc;
    color: #fcfcfc;
  }

  &.color-unchecked {
    background-color: #07484b;
    border-color: #fcfcfc;
    color: #fcfcfc;
  }
}

.tracking-item {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid;
  border-radius: 5px;
  padding: 10px 20px;
}

.tracking-item-menu {
  height: 45px;
  display: flex;
  justify-content: space-between;
  transition: height 0.3s linear;
}

.tracking-item-menu-item {
  cursor: pointer;
  border: 1px solid;
  border-top: none;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hidden-menu {
  height: 0;
}

.visible-menu-item {
  opacity: 1;
  transition: opacity 0.1s linear 0.2s;
}

.hidden-menu-item {
  opacity: 0;
  transition: opacity;
}
