.item-form {
  padding: 40px 20px;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 1px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.item-label {
  color: #fcfcfc;
  font-size: 20px;
  text-align: left;
  width: 100%;
  max-width: 400px;
  margin: 0 auto 20px auto;
}

.item-input {
  width: 100%;
  max-width: 400px;
  padding: 10px;
  background-color: #042a2b;
  color: #42b883;
  border: 1px solid #42b883;
  margin-bottom: 40px;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 1px;
}

.item-submit {
  @media screen and (min-width: 800px) {
    &:hover {
      background-image: linear-gradient(to bottom, #4FC590, #5CD29D);
    }

    &:active {
      transform: scale(0.98);
      outline: none;
    }
  }

  cursor: pointer;
  width: 55px;
  height: 55px;
  background-image: linear-gradient(to bottom, #42B883, #4FC590);
  color: #fcfcfc;
  font-size: 22px;
  border-radius: 50%;
  border: none;
  outline: none;
  box-shadow: 0px 6px 12px 0px rgba($color: #000000, $alpha: 0.2);
}