$black: #042a2b;
$green: #42b883;
$white: #fcfcfc;

.App {
  position: relative;
  text-align: center;
  margin: 0;
  min-height: 100%;
}

.main {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 25px;
  flex-direction: column;
  justify-content: space-between;
}

.refresh-button {
  
  cursor: pointer;
  height: 70px;
  width: 70px;
  background-color: #1e90ff;
  color: #fefefe;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  outline: none;
  
  @media screen and (min-width: 800px){
    &:hover {
      background-color: #379bff;
    }

    &:active {
      transform: scale(0.98);
    }
  }
}

.initial-create-container {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 25px;
  justify-content: center;
  align-items: center;
}

.initial-create-button {
  cursor: pointer;
  color: $green;

   div {
     font-size: 18px;
     font-weight: 300;
     margin-top: 20px;
   }
}
